	module.exports = {"data":{"markdownRemark":{"html":"<p>1.1 What is JavaScript?</p>\n<p>JavaScript is programming language which tell computer to do certain things. It was introduced in 1995. Today it is the true programming language of the web. All web browsers understands JavaScript it allows direct interaction without doing a page reload for every action. It started as a way to make page more interactive - to add behavior. Nowadays JavaScript runs in more places than just web browsers, it runs on web services, phone and even robots!  </p>\n<p>1.2. Why JavaScript?</p>\n<p>JavaScript is a language with enormous expressive power. It is possible to get work done without knowing much about the language or even knowing much about programming. The idea behind design was that it would make programming in JavaScript easier for beginners.</p>\n<p>JavaScript is a beautiful, elegant, highly expressive language. It is easy to learn and very functional. It has dynamic typing and first class function, which help its support not just object oriented, but also imperative and functional programming styles.</p>\n<p>1.3. What is ECMAScript in JavaScript?</p>\n<p>1.4. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-htm\"><code class=\"language-htm\">&lt;h1&gt;This is a code example. It&#39;s not editable&lt;/h1&gt;</code></pre>\n      </div>\n<p>Then there are code samples like this that have white backgrounds and are in two pieces. These are editable and you can see the results instantly! Check it out:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>This is a code example. It is editable!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<p>You'll see these throughout. If you see one that is editable, please play with it. You'll learn a lot by experimentation.</p>","frontmatter":{"path":"/overview","title":"Introduction to JavaScript?","order":1}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"order":0,"path":"/intro","title":"Introduction to the Course"}}},{"node":{"frontmatter":{"order":1,"path":"/overview","title":"Introduction to JavaScript?"}}},{"node":{"frontmatter":{"order":2,"path":"/tools","title":"Systems"}}},{"node":{"frontmatter":{"order":3,"path":"/variable","title":"What is Variable"}}},{"node":{"frontmatter":{"order":4,"path":"/html-next-steps","title":"HTML Next Steps"}}},{"node":{"frontmatter":{"order":5,"path":"/meta-html","title":"Meta HTML"}}},{"node":{"frontmatter":{"order":6,"path":"/basic-css","title":"Basic CSS"}}},{"node":{"frontmatter":{"order":7,"path":"/selectors","title":"CSS Selectors and the Cascade"}}},{"node":{"frontmatter":{"order":8,"path":"/layout-css","title":"Layout CSS"}}},{"node":{"frontmatter":{"order":9,"path":"/css-patterns","title":"Effective Patterns for Writing CSS"}}},{"node":{"frontmatter":{"order":10,"path":"/project-html-css","title":"Project: HTML & CSS"}}},{"node":{"frontmatter":{"order":11,"path":"/programming-fundamentals","title":"Programming Fundamentals"}}},{"node":{"frontmatter":{"order":12,"path":"/functions-and-scope","title":"Functions and Scope"}}},{"node":{"frontmatter":{"order":13,"path":"/objects-and-arrays","title":"Objects and Arrays"}}},{"node":{"frontmatter":{"order":14,"path":"/dom","title":"The DOM"}}},{"node":{"frontmatter":{"order":15,"path":"/js-project","title":"JavaScript, HTML, and CSS Project"}}},{"node":{"frontmatter":{"order":16,"path":"/ajax","title":"AJAX"}}},{"node":{"frontmatter":{"order":17,"path":"/libraries","title":"Integrating with Other People's Libraries"}}},{"node":{"frontmatter":{"order":18,"path":"/git-and-bash","title":"Git and Bash"}}},{"node":{"frontmatter":{"order":19,"path":"/node","title":"Node.js"}}},{"node":{"frontmatter":{"order":20,"path":"/deploying","title":"Deploying Your App"}}},{"node":{"frontmatter":{"order":21,"path":"/final-thoughts","title":"Final Thoughts"}}}]}},"pathContext":{}}

